.placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.placeholder-logo {
  animation: placeholder-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

@keyframes placeholder-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
